
.vsv-main{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;background:#000;color:#000;min-height:50vh
}
.vsv-main .vsv-flow{-ms-flex:1;flex:1;-ms-flex-order:2;order:2;background:#fff;padding:10px 50px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
.vsv-main .vsv-columns{display:-ms-flexbox;display:flex;-ms-flex:1;flex:1
}
.vsv-main .vsv-sidebar{width:18%;text-transform:uppercase;-ms-flex:0 0 240px;flex:0 0 240px
}
.vsv-sidebar-right{-ms-flex-order:1;order:1;background:#f0f0f0
}
.vsv-sidebar-left{-ms-flex-order:3;order:3;background:#26292e
}
.vsv-sidebar-title{height:40px;line-height:40px;background:#26292e;color:#fff;font-size:14px
}
hr{color:#cdcdcd;margin-bottom:20px;width:100%
}
hr .sidebar{color:#fff
}
.a50{width:50%
}
ol{list-style:none !important;padding-left:0
}
.vsv-bread{font-size:14px;font-weight:500;text-align:left;text-transform:uppercase;margin-bottom:15px;display:block
}
.vsv-btn-primary{font-size:14px;font-weight:400;background-color:#0053f5;width:220px;border-radius:12px;border:none;padding:10px 0;color:#ffffff;text-transform:uppercase;margin:0 auto;transform:translateY(35px);background:linear-gradient(#0082fe, #0052f6)
}
.vsv-btn-gray-sm{font-size:14px;font-weight:500;width:120px;border-radius:12px;border:none;padding:10px 0;color:#ffffff;text-transform:uppercase;transform:translateX(30px);background:linear-gradient(#a6afbf, #80899a);text-align:center
}
.vsv-btn-gray-lg{font-size:14px;font-weight:500;width:220px;border-radius:12px;border:none;padding:10px 0;color:#ffffff;text-transform:uppercase;background:linear-gradient(#a6afbf, #80899a);text-align:center
}
.vsv-internal-link,.vsv-internal-link:hover,.vsv-internal-link:active,.vsv-internal-link:visited{text-decoration:none;color:#fff
}
.vsv-sidebar-jobs-selected{padding-left:18%;margin:0 auto;color:#fff;padding-top:10px
}
.vsv-sidebar-jobs-selected ol>li{text-transform:uppercase;margin:5px 5px 15px 5px
}
.vsv-sidebar-jobs-selected label{display:block;padding-left:15px;text-indent:-15px
}
.vsv-sidebar-jobs-selected .vsv-jobs-selected-job{padding-left:11%;text-transform:capitalize
}
.vsv-sidebar-jobs-selected .vsv-jobs-selected-location{padding-left:11%;text-transform:capitalize
}
.jb{text-align:center
}
.vsv-sidebar-jobs-av{text-align:left
}
.vsv-chefs-cb{width:16px;vertical-align:middle;position:relative;top:-2px
}
[type="checkbox"]{display:none
}
.vsv-rest-cats{text-transform:capitalize
}
.vsv-rest-cats #vsv-cats-list{padding-left:22px;font-weight:500;font-size:14px;text-transform:uppercase
}
.vsv-rest-cats .vsv-rest-cats-list li{padding:2px 0 2px 20px;color:#0053f5;font-weight:500;border-left:4px solid #f0f0f0
}
.vsv-rest-cats .vsv-rest-cats-list li:active{color:#000;background:#fff;border-left:4px solid #000
}
.vsv-rest-cats .vsv-rest-cats-list li:hover{color:#000;background:#fff;border-left:4px solid #000
}
body{box-sizing:border-box
}
body{margin:0;padding:0
}
#app{width:100%;font-family:"acumin-pro",sans-serif;font-style:14px;font-weight:100;letter-spacing:.6px
}



